import {useEffect, useState} from "react";
import "./FormInput.css"

function WifiInput(props) {
    const [type,setType]=useState("WPA")
    const [ssid,setSsid]=useState("")
    const [pass,setPass]=useState("")


    const isSpecialChar = (c)=>{
        return c==='\\' || c===';' || c===',' || c==='"' || c===':'
    }

    const esc = (s) => {
        return s.split("").map(c=>isSpecialChar(c)?'\\'+c : c).join("");
    }

    const toText = () => {
        if (ssid.trim().length===0) return ''
        const passPart = (type!=="nopass") ? `P:${esc(pass)};;` : ""
        return `WIFI:T:${type};S:${esc(ssid)};${passPart}`
    }

    useEffect(() => {
        props.onChanged(toText())
    }, [type,ssid,pass])

    return <div className={"form"}>
        <div className={"prop"}>
            <div className={"title"}>Type</div>
            <select value={type} onChange={(e)=>setType(e.target.value)}>
                <option value={"WPA"}>WPA</option>
                <option value={"WEP"}>WEP</option>
                <option value={"nopass"}>Open</option>
            </select>
        </div>
        <div className={"prop"}>
            <div className={"title"}>SSID</div>
            <input type="text" value={ssid} onChange={(e) => setSsid(e.target.value)}/>
        </div>

        { type!=='nopass' &&
        <div className={"prop"}>
            <div className={"title"}>PASS</div>
            <input type="text" value={pass} onChange={(e) => setPass(e.target.value)}/>
        </div>
        }
    </div>
}

export default WifiInput
