import {useEffect, useState} from "react";
import "./FormInput.css"

function MailInput(props) {
    const [value, setValue] = useState({
        to: "",
        subject: "",
        cc: "",
        bcc: "",
        body:""
    })

    const toText = () => {
        const params=["subject","cc","bcc","body"]
            .filter(prop=>value[prop].trim().length>0)
            .map(prop=>prop+"="+value[prop])
            .join("&")

        if (value.to.trim().length>0) {
            return (params.length>0) ? `mailto:${value.to}?${params}` : `mailto:${value.to}`
        } else {
            return ""
        }
    }

    useEffect(() => {
        props.onChanged(toText(value))
    }, [value])

    const onChange = (prop, e) => {
        const v = e.target.value
        const newValue = {...value}
        newValue[prop] = v
        setValue(newValue)
    }

    return <div className={"form"}>
        {["to","subject","cc","bcc","body"].map(prop=> <div className={"prop"}>
                <div className={"title"}>{prop.toUpperCase()}</div>
                <input type="text" value={value[prop]} onChange={(e) => onChange(prop, e)}/>
            </div>)
        }
    </div>
}

export default MailInput
